.footer_container{
    background-color: #231f20 !important;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #cfcfcf;

}
.footer_box{
    width: 65%;
    height: inherit;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 50px;

    @media only screen and (max-width: 1500px) {
        width: 70%;
    }
    @media only screen and (max-width: 1300px) {
        width: 75%;
    }
}
.footer_about{

    display: flex;
    height: 300px;
    width: 100%;
    justify-content: center;

    @media only screen and (max-width: 1500px) {
        height: 250px;
    }
}
%data_box{
    width: 25%;

    @media only screen and (max-width: 1500px) {
        width: 22%;
    }
}

.branch_box{
    @extend %data_box;
}

.footer_data{

    border-right: 3px solid #cfcfcf;
    list-style: none;
    height: 150px;

    @media only screen and (max-width: 1500px) {
        border-right: 2px solid #cfcfcf;
    }

    li{
        display: list-item;
        text-align: -webkit-match-parent;
        padding-right: 10px;
        padding-left: 10px;
        
        @media only screen and (max-width: 1500px) {
            padding-right: 8px;
            padding-left: 8px;

        }
        b{
            float: left;
            min-width: 90px;
            font-weight: bold;
            width: min-content;
            line-height: 20px;

        @media only screen and (max-width: 1500px) {
            min-width: 80px;
            font-size: 14px;
        }
        }
        p{
            overflow: auto;
            
            @media only screen and (max-width: 1500px) {
                font-size: 13px;
            }
        }
        a{
            outline: none;
            text-decoration: none;
            color: inherit;
            
            @media only screen and (max-width: 1500px) {
                font-size: 13px;
            }
            
        }
        input{
            background-color: #231f20;
            border: 2px solid #cfcfcf;
            outline: none;
            color: #cfcfcf;
            padding: 5px;
            margin-bottom: 10px;
            margin-top: 10px;
            
            @media only screen and (max-width: 1500px) {
                border: 1.5px solid #cfcfcf;
                padding: 3px;
                margin-bottom: 8px;
                margin-top: 8px;
                width: 120px;
            }
        }
        button{
            padding:5px;
            background-color: #ed1c24;
            outline: none;
            border: 2px solid #ed1c24;
            border-radius: 6px ;
            color: #cfcfcf;
            cursor: pointer;
            
            @media only screen and (max-width: 1500px) {
                border: 1.5px solid #ed1c24;
                border-radius: 5px ;
                padding: 4px;
                font-size: 12px;

            }

        }
        img{
            height: 40px;
        }
        .payment{
            width: 100%;
            font-size: 14px;
        }
    }
}

.contact_box , .company_box , .news_box{
    @extend %data_box;
}
.news_box ul{
    border-right:none;
}

h3{
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
}


.copyright_box{
    width: 100%;
    // padding-top:  50px;
    hr{
        margin-bottom: 10px;
    }
    a{
        color: red;
        text-decoration: none;
    }
}

.fright{
    display: flex;
    justify-content: center;
    text-align: center;
    float: right;
    margin-right: 30px;
    .icons{
        *{
            margin-right: 5px;
            cursor: pointer;
        }
    }
}