@import url('https://fonts.googleapis.com/css2?family=Khula&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Khula', sans-serif;
}

.header{
    display: flex;
    flex-direction: column;
}

.header_contact{
    height: 40px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #231f20;
    color: #f3ffff;
    
    @media only screen and (max-width: 1500px) {
        font-size: 15px;
    }
    @media only screen and (max-width: 1400px) {
        font-size: 14px;
    }
}
.call{
    margin-right: 40%;

    @media only screen and (max-width: 1500px) {
        margin-right: 30%;
    }
    @media only screen and (max-width: 1400px) {
        margin-right: 28%;
    }
}

.header_contant{
    display: flex;
    justify-content: center;
}
.header_contant .container{
    width: 1200px;
    display: flex;
    align-items: center;

    @media only screen and (max-width: 1500px) {
        width: 1000px;
    }
    @media only screen and (max-width: 1400px) {
        width: 900px;
    }
}
.img{
    height: 80px;
    width: 160px;

    @media only screen and (max-width: 1500px) {
        width: 140px;
        height: 60px;
    }
    @media only screen and (max-width: 1400px) {
        width: 130px;
        // height: 50px;
    }
}
.search{
    margin-left: 30%;
    padding: 8px 190px 8px 5px;
    @media only screen and (max-width: 1500px) {
        margin-left: 20%;
        padding: 6px 160px 6px 3px;
    }
    @media only screen and (max-width: 1400px) {
        margin-left: 15%;
        padding: 5px 140px 5px 3px;
    }
}
.help{
    display: flex;
    justify-content: center;
    margin-left: 10px;
    text-decoration: none;
    color: #4d4d4d;

}
.help .icon{
    font-size: 40px;
    color: #231f20;
    @media only screen and (max-width: 1500px) {
        font-size: 35px;
        margin-top: 10px;
    }
    @media only screen and (max-width: 1400px) {
        font-size: 30px;
        margin-top: 10px;
    }
}
.help .mes{
    width: 60px;
    font-size: 18px;
    @media only screen and (max-width: 1500px) {
        font-size: 15px;
        width: 50px;
        margin-top: 8px;
    }
    @media only screen and (max-width: 1400px) {
        font-size: 13px;
        width: 40px;
        margin-top: 8px;
    }
}

.login{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 10px;
    text-decoration: none;
    color: #4d4d4d;

}
.login .icon{
    font-size: 40px;
    color: #231f20;
    @media only screen and (max-width: 1500px) {
        font-size: 35px;
        margin-top: 10px;
    }
    @media only screen and (max-width: 1400px) {
        font-size: 30px;
        margin-top: 10px;
    }
}
.login .mes{
    width: 60px;
    font-size: 18px;
    @media only screen and (max-width: 1500px) {
        font-size: 15px;
        width: 50px;
        margin-top: 8px;
    }
    @media only screen and (max-width: 1400px) {
        font-size: 13px;
        width: 40px;
        margin-top: 6px;
    }
}

.cart{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 10px;
    text-decoration: none;
    color: #4d4d4d;

}
.cart .icon{
    font-size: 40px;
    color: #231f20;
    @media only screen and (max-width: 1500px) {
        font-size: 35px;
        margin-top: 10px;
    }
    @media only screen and (max-width: 1400px) {
        font-size: 30px;
        margin-top: 10px;
    }
}
.cart .mes{
    width: 60px;
    font-size: 18px;
    @media only screen and (max-width: 1500px) {
        font-size: 15px;
        width: 50px;
        margin-top: 8px;
    }
    @media only screen and (max-width: 1400px) {
        font-size: 13px;
        width: 40px;
        margin-top: 6px;
    }
}


nav{
    display: flex;
    justify-content: center;
    padding: 10px 0px;
    border-top: 2px solid black;
    border-bottom: 2px solid black;
    
    @media only screen and (max-width: 1500px) {
        padding: 7px 0px;
    }
    @media only screen and (max-width: 1400px) {
        padding: 6px 0px;
    }

}
nav ul{
    display: flex;
    flex-direction: row;
    list-style: none;
}
nav ul li{
    padding: 3px;
    margin:1px 5px ;

    @media only screen and (max-width: 1500px) {
        padding: 2px;
        margin: 1px 3px;
        font-size: 14px;
    }
    @media only screen and (max-width: 1400px) {
        padding: 1.5px;
        margin: 1px 2px;
        font-size: 13px;
    }
}
nav ul li a{
    text-decoration: none;
    color: black;
    font-weight: bold;
}